.App {
  text-align: left;
  /* width: 30%;
  height: 30%; */
  margin-bottom: 100px;
}

.button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.App-footer {
  margin: 0px;
  /* background-color: #000000; */
  min-height: 150vh;
  display: flex;
  flex-direction: column;
  /* padding: 20px; */
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
